<template>
    <div class="content-bg"   v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
      <div class="top-title">广播包/组播包</div>
      <div style="padding: 0 5px;"><v-basedate class="from-wapper" busName="mulAndBroDateBus2"></v-basedate></div>
      <div class="item-wapper">
        <div class="item-title">全局-广播包、组播包</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.allMulAndBroData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vBasedate from '../common/baseDate'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "mulAndBro",
        data() {
            return {
              fullscreenLoading:false,
              //全局广播包组播包个数
              allMulAndBroData:{
                baseConfig:{
                  title:'',
                  xAxisName:'时间',
                  yAxisName:'个数',
                  hoverText:'',
                },
                yAxisArr:[{
                  name:'组播包',
                  color:'rgb(15,219,243)',
                  data:[]
                },{
                  name:'广播包',
                  color:'rgb(9,243,105)',
                  data:[]
                }]
              },
              allMulAndBroData2:{
                baseConfig:{
                  title:'',
                  xAxisName:'类型',
                  yAxisName:'数据包/个数',
                  rotate:'20',
                  itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                },
                xAxisArr:['组播包','广播包'],
                yAxisArr:[]

              }
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('mulAndBroDateBus2',(arr)=>{
              //定义参数
              let paramObj = {
                starttime:arr[0],
                endtime:arr[1]
              }
              /*获取全局数据包类型个数*/
              this.fullscreenLoading = true;
              this.getAllMulAndBroData(paramObj);
            })
        },
        mounted(){},
        methods:{
            /*获取广播包组播包个数*/
            getAllMulAndBroData(paramObj){
                this.$nextTick(()=> {
                    this.$post('/flow/getMulticastAndBroadcastPacketTypeCount.do', paramObj)
                        .then(res => {
                          this.fullscreenLoading = false;
                          let arr = [];
                          arr.push(res[0].multicast.value[1]);
                          arr.push(res[0].broadcast.value[1]);
                          this.allMulAndBroData2.yAxisArr= arr;
                        })
                        .catch(err => {
                          this.fullscreenLoading = false;
                        })
                })
            },
        },
        components:{
          vBasedate,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



